<template>
  <div class='container'>
      <el-dialog width="53%" @close="closeDialog" title="题目预览" :visible="showPreview">
      <div class="el-row">
              <div class="info">
                <div>【题型】：{{ randomsDetail.questionType === '1' ? '单选题' : randomsDetail.questionType === '2' ? '多选题' : '简答题' }}</div>
                <div>【编号】：{{ randomsDetail.id }}</div>
                <div>【难度】：{{ randomsDetail.difficulty === '2' ? '一般' : randomsDetail.difficulty === '3' ? '困难' : '简单' }}</div>
                <div>【标签】：{{ randomsDetail.tags }}</div>
                <div>【学科】：{{ randomsDetail.subjectID === 4 ? '前端' : 'java' }}</div>
                <div>【目录】：{{ randomsDetail.catalogID === 1 ? 'java基础' : randomsDetail.catalogID === 2 ? 'javaWEB' : randomsDetail.catalogID === 6 ? 'js基础' : randomsDetail.catalogID === 5 ? 'css基础' : 'vue基础' }}</div>
                <div>【方向】：{{ randomsDetail.direction }}</div>
              </div>
              <hr> 【题干】：
              <div class="toggle">
              <div class="toggle-content" v-html="randomsDetail.question">
              </div>
              <!-- 根据题型不同切换题干内容 -->
              <!-- 多选 -->
              <div v-if="randomsDetail.questionType === '2'">
                <div class="notice">多选题 选项：（以下选中的选项为正确答案）</div>
                <div style="padding: 8px 0px;" v-for="(item, index) in randomsDetail.options" :key="index">
                  <el-checkbox :checked="item.isRight === 0" >{{ item.title }}</el-checkbox>
                </div>
              </div>
              <!-- 单选 -->
              <div v-else-if="randomsDetail.questionType === '1'">
                <div class="notice">单选题 选项：（以下选中的选项为正确答案）</div>
                <el-radio-group v-model="radio">
                  <div v-for="(item, index) in randomsDetail.options" :key="index">
                    <el-radio :label="item.isRight">{{ item.title }}</el-radio>
                  </div>
              </el-radio-group>
              </div>
              <!-- 简答 -->
              <div v-else>
              </div>

              </div>
              <hr>
              <div>【参考答案】：
                <el-button @click="showVideo(randomsDetail.videoURL)" type="danger">视频答案预览</el-button>
              </div>
              <div v-if="videoShow && randomsDetail.videoURL" class="video">
                <video controls="controls" :src="randomsDetail.videoURL" autoplay width="300" height="200"></video>
              </div>
              <hr>
                <div class="answerHtml" v-html="`【答案解析】:${randomsDetail.answer}`"></div>
              <hr>
              <div>【题目备注】：{{ randomsDetail.remarks }}</div>

        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="closeDialog" type="primary">关闭</el-button>
        </div>
      </el-dialog>
  </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex'
export default {
  computed: {
    ...mapState('questionsChoice/questionsRandoms', ['randomsDetail', 'showPreview'])
  },
  data () {
    return {
      videoShow: false,
      radio: 1
    }
  },
  methods: {
    ...mapMutations('questionsChoice/questionsRandoms', ['setShowPreview']),
    closeDialog () {
      this.videoShow = false
      this.setShowPreview(false)
    },
    showVideo (url) {
      this.videoShow = true
      if (!url) {
        this.$message.error('当前题目无视频')
      }
    }
  }

}
</script>

<style scoped lang='less'></style>
