<template>
  <div class='container'>
    <!-- 顶部关键词搜索 -->
    <div class="header">
      <div class="left">
        <span>关键字</span>
        <el-input style="width:241px" v-model="input" placeholder="根据编号搜索"></el-input>
      </div>
      <div class="right">
        <el-button @click="clear">清除</el-button>
        <el-button @click="search" type="primary">搜索</el-button>
      </div>
    </div>

    <!-- 条数提示栏 -->
    <el-alert
      :title="`数据一共 ${listCounts} 条`"
      type="info"
      show-icon>
    </el-alert>

    <!-- table列表 -->
    <el-table
      :header-cell-style="{background:'#fafafa'}"
      :data="randomsList"
      style="width: 100%">
      <el-table-column
        label="编号"
        width="220">
        <template slot-scope="scope">
          <span>{{ scope.row.id }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="题型"
        width="80">
        <template slot-scope="scope">
          <span>{{ scope.row.questionType==='1'?'多选': scope.row.questionType==='2'?'简答':'单选'}}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="题目编号"
        width="220">
        <template slot-scope="scope">
          <div v-for="(item) in scope.row.questionIDs" :key="item.number" >
            <a @click="look(item)" style="color: rgb(0, 153, 255);" href="javascript:;">{{item.number}}</a>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        label="录入时间"
        width="180">
        <template slot-scope="scope">
          <span>{{ scope.row.addTime }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="答题时间(s)"
        min-width="165">
        <template slot-scope="scope">
          <span>{{ scope.row.totalSeconds }}</span>
        </template>
      </el-table-column>
      <el-table-column
        min-width="165"
        label="正确率(%)"
        >
        <template slot-scope="scope">
          <span>{{ scope.row.accuracyRate }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="录入人"
        min-width="165"
        >
        <template slot-scope="scope">
          <span>{{ scope.row.userName }}</span>
        </template>
      </el-table-column>
      <el-table-column width="80" label="操作">
        <template slot-scope="scope">
          <el-button plain type="danger" icon="el-icon-delete" circle @click="handleDelete(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- 预览功能：点击编号打开题目详情对话框 -->
    <QuestionsPreview></QuestionsPreview>

    <!-- 底部分页器 -->
    <el-pagination
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[20, 30, 50, 100]"
      :page-size="20"
      layout="prev, pager, next, sizes, jumper"
      :total="listCounts">
    </el-pagination>
  </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex'
import QuestionsPreview from '../components/questions-preview.vue'
export default {
  components: {
    QuestionsPreview
  },
  data () {
    return {
      input: '',
      tableData: this.randomsList || [],
      currentPage: 1,
      pageSize: 20,
      formData: this.randomsDetail || {},
      dialogFormVisible: false,
      radio: 1,
      videoShow: false
    }
  },
  methods: {
    ...mapActions('questionsChoice/questionsRandoms', ['getRandomsListAsync', 'delRandomsListAsync', 'lookDetailAsync']),
    ...mapMutations('questionsChoice/questionsRandoms', ['setShowPreview']),
    // 删除列表项
    handleDelete (index, row) {
      // console.log(index, row)
      this.$confirm('此操作将永久删除该题组, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        await this.delRandomsListAsync(row)
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      }).catch(() => {})
    },
    // 切换页面大小
    async handleSizeChange (val) {
      // console.log(`每页 ${val} 条`)
      this.pageSize = val
      await this.getRandomsListAsync({
        page: 1,
        pagesize: val,
        keyword: this.input
      })
    },
    // 切换页面
    async handleCurrentChange (val) {
      // console.log(`当前页: ${val}`)
      this.currentPage = val
      await this.getRandomsListAsync({
        page: val,
        pagesize: this.pageSize,
        keyword: this.input
      })
    },
    // 关键字搜索
    async search () {
      await this.getRandomsListAsync({
        page: 1,
        pagesize: this.pageSize,
        keyword: this.input
      })
    },
    // 清空搜索框
    clear () {
      this.input = ''
    },
    // 点击编号预览功能
    async look (item) {
      await this.lookDetailAsync(item)
      // this.dialogFormVisible = true
      this.setShowPreview(true)
    },
    // 关闭Dialog时关闭隐藏视频
    closeDialog () {
      this.videoShow = false
    }
  },
  created () {
    this.getRandomsListAsync()
  },
  computed: {
    ...mapState('questionsChoice/questionsRandoms', ['randomsList', 'listCounts', 'randomsDetail'])
  }
}
</script>

